}

.paginationBox {
  display: flex;
  justify-content: center;
  margin: 20px 0;
  /* Increased margin for better spacing */
}

.pagination {
  display: flex;
  justify-content: center;
  padding: 0;
}

.page-item {
  margin: 0 5px;
  /* Add spacing between pagination items */
  list-style: none;
  /* Remove default list styling */
}

.page-link {
  color: #333;
  /* Set text color */
  background-color: #fff;
  /* Set background color */
  border: 1px solid #ccc;
  /* Add border to page links */
  border-radius: 20px;
  /* Round the corners */
  padding: 8px 12px;
  /* Add padding for better click area */
  text-decoration: none;
  /* Remove default underline */
  transition: background-color 0.3s ease, color 0.3s ease;
  /* Add transition for smooth hover effect */
  font-family: 'Arial', sans-serif;
  /* Choose an elegant font */
}

.page-link:hover {
  background-color: #f0f0f0;
  /* Change background color on hover */
}

.pageItemActive .page-link {
  background-color: #007bff;
  /* Change background color of active page */
  color: #fff;
  /* Change text color of active page */
  border-color: #007bff;
  /* Change border color of active page */
}


.page-item {
  background-color: rgb(255, 255, 255);
  list-style: none;
  border: 1px solid rgba(0, 0, 0, 0.178);
  padding: 1vmax 1.5vmax;
  transition: all 0.3s;
  cursor: pointer;
}

.page-item:first-child {
  border-radius: 5px 0 0 5px;
}

.page-item:last-child {
  border-radius: 0 5px 5px 0;
}

.page-link {
  text-decoration: none;
  font: 300 0.7vmax "Roboto";
  color: rgb(80, 80, 80);
  transition: all 0.3s;
}

.page-item:hover {
  background-color: rgb(230, 230, 230);
}

.page-item:hover .page-link {
  color: rgb(0, 0, 0);
}

.pageItemActive {
  background-color: tomato;
}

.pageLinkActive {
  color: rgb(0, 0, 0);
}

@media screen and (max-width: 600px) {
